/* SLIDER NAVIGATION */
.custom-slider-navigation {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
  gap: 0.3rem;
}
.custom-slider-navigation button {
  cursor: pointer;
  width: 12px;
  height: 12px;
  border: none;
  background-color: white;
  outline: none !important;
  padding: 0;
  border: 1px solid #8E9BAA;
  border-radius: 50%;
}
.custom-slider-navigation button:hover, .custom-slider-navigation button.bullet-active {
  background-color: #8E9BAA;
}
.custom-slider-navigation button:first-child {
  margin-left: 0;
}
.custom-slider-navigation button:last-child {
  margin-right: 0;
}

img {
  border-radius: 4px;
}

.author-info {
  display: flex;
  align-items: center;
  margin: 20px 0px 0px 0px;
  font-size: 0.75rem;
  flex-wrap: wrap;
}
.author-info a {
  text-decoration: none;
}
.author-info img {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  margin-right: 0.2rem;
}
.author-info .reviewed-by, .author-info .last-mod-date {
  padding-left: 0.5rem;
  display: flex;
  align-items: center;
}
.author-info .reviewed-by::before, .author-info .last-mod-date::before {
  content: "";
  padding-right: 0.5rem;
  position: relative;
  top: -1px;
}
.author-info .reviewed-by .shield, .author-info .reviewed-by .calender, .author-info .last-mod-date .shield, .author-info .last-mod-date .calender {
  padding-right: 0.2rem;
}
.author-info .reviewed-by .shield svg, .author-info .reviewed-by .calender svg, .author-info .last-mod-date .shield svg, .author-info .last-mod-date .calender svg {
  display: block;
  position: relative;
  top: -1px;
}

.author-box {
  display: flex;
  background: rgb(30, 115, 190);
  background: linear-gradient(90deg, rgb(30, 115, 190) 0%, rgb(30, 115, 190) 40%, rgb(100, 159, 246) 100%);
  color: #fff;
  align-items: center;
  border-radius: 4px;
  padding: 20px;
  gap: 20px;
  margin-top: 40px;
}
@media (max-width: 500px) {
  .author-box {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
.author-box .author-box-avatar {
  flex: 0 0 60px;
}
.author-box .author-box-avatar img {
  border-radius: 50%;
  display: block;
}
.author-box .author-box_name {
  font-size: 1.3rem;
  font-weight: 600;
}
.author-box .author-box_bio {
  font-size: 1rem;
  font-weight: 600;
}

.main-navigation {
  border-bottom: 1px solid var(--border);
}
@media (min-width: 960px) {
  .main-navigation {
    border-bottom: none;
  }
}
.main-navigation #primary-menu {
  border-top: 1px solid var(--border);
  border-bottom: 1px solid var(--border);
}
@media (min-width: 1px) and (max-width: 960px) {
  .main-navigation #primary-menu {
    border-bottom: none;
  }
}
.main-navigation #primary-menu li a {
  text-align: center;
}
@media (min-width: 960px) {
  .main-navigation #primary-menu li:hover a {
    font-weight: 700;
    -webkit-box-shadow: inset 0px -2px 0px 0px rgb(0, 0, 0);
    -moz-box-shadow: inset 0px -2px 0px 0px rgb(0, 0, 0);
    box-shadow: inset 0px -2px 0px 0px rgb(0, 0, 0);
  }
}

/* Blog Single Content */
#primary .entry-content figure {
  margin-bottom: 20px;
}

@media (min-width: 960px) {
  .navigation-branding, .main-nav {
    width: 100%;
    justify-content: center;
  }
  #menu-hauptmenu {
    justify-content: center;
  }
}
/* Styles for Archives */
@media (min-width: 1200px) {
  body:not(.single, .page) #primary article.post .inside-article, .featured-posts article.post .inside-article {
    display: flex;
    gap: 40px;
  }
}
body:not(.single, .page) #primary article.post .inside-article a, .featured-posts article.post .inside-article a {
  text-decoration: none;
}
body:not(.single, .page) #primary article.post .inside-article .archive-single-featured-image, .featured-posts article.post .inside-article .archive-single-featured-image {
  border-radius: 5px;
}
@media (min-width: 1200px) {
  body:not(.single, .page) #primary article.post .inside-article .archive-single-featured-image, .featured-posts article.post .inside-article .archive-single-featured-image {
    width: 50%;
  }
}
@media (min-width: 1200px) {
  body:not(.single, .page) #primary article.post .inside-article .archive-single-content, .featured-posts article.post .inside-article .archive-single-content {
    width: 50%;
  }
}
body:not(.single, .page) #primary article.post .inside-article .archive-single-content .archive-single-category, .featured-posts article.post .inside-article .archive-single-content .archive-single-category {
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.82px;
  color: #8E9BAA;
}
body:not(.single, .page) #primary article.post .inside-article .archive-single-content .entry-title, .featured-posts article.post .inside-article .archive-single-content .entry-title {
  font-size: 1.4rem;
  font-weight: 600;
  margin-top: 10px;
}
body:not(.single, .page) #primary article.post .inside-article .archive-single-content .entry-summary, .featured-posts article.post .inside-article .archive-single-content .entry-summary {
  font-size: 1rem;
  margin: 25px 0px 10px 0px;
}
body:not(.single, .page) #primary article.post .inside-article .archive-single-content .read-more a, .featured-posts article.post .inside-article .archive-single-content .read-more a {
  text-transform: uppercase;
  display: block;
  padding: 15px 0;
  border-top: 1px solid var(--border);
  font-size: 0.75rem;
  font-weight: 700;
}

body.single-post .author-info {
  justify-content: center;
  margin: 36px 0px 0px 0px;
}

/* END - Styles for Archives */
.inside-article .featured-image img {
  width: 100%;
}

/* Search bar */
.wp-block-search__inside-wrapper {
  border: none;
}
.wp-block-search__inside-wrapper input {
  border-radius: 3px 0 0 3px;
}
.wp-block-search__inside-wrapper button {
  text-shadow: none;
  margin-left: 0px;
  border-radius: 0 3px 3px 0;
  padding: 7px 10px 5px;
}

.site-footer .footer-widgets .inside-footer-widgets hr {
  max-width: 30px;
  margin: 0px;
}
.site-footer .footer-widgets .widget {
  margin-bottom: 10px;
}
.site-footer .footer_headline {
  color: var(--white);
  padding-bottom: 5px;
  font-size: 17px;
  font-weight: 700;
  margin-bottom: 10px;
  margin-top: 0;
}
.site-footer .footer-widgets a {
  text-decoration: none;
}

/* Pagination */
.paging-navigation .nav-links > * {
  font-size: 0.9rem;
  background: var(--accent-2);
  color: inherit;
  padding: 10px 15px !important;
  border-bottom: 0;
  text-decoration: none;
  border-radius: 3px;
}

.paging-navigation .nav-links .current, .paging-navigation .nav-links a:hover {
  -moz-opacity: 0.6;
  opacity: 0.6;
}

/* Style page title */
.page-header, .home-headline {
  padding-bottom: 0px !important;
}
.page-header h1, .page-header h2, .home-headline h1, .home-headline h2 {
  border-bottom: 1px solid var(--border);
}

.home-headline > div:first-child {
  padding-bottom: 0px;
}

/* Post Single */
body.single-post h1.entry-title {
  text-align: center;
}
body.single-post h1.entry-title::after {
  display: block;
  height: 2px;
  background: var(--accent-2);
  width: 80px;
  content: " ";
  margin: 9px auto 20px auto;
}
body.single-post .featured-image {
  margin-bottom: 25px !important;
}

/* Add ".." after excerpt */
.entry-summary p:after {
  content: "..";
}

/* 404 Page */
body.error404 .entry-title center:after {
  display: block;
  height: 2px;
  background: var(--accent-2);
  width: 80px;
  content: " ";
  margin: 9px auto 20px auto;
}
body.error404 .entry-content > article > .inside-article {
  padding-left: 0px;
  padding-right: 0px;
}
body.error404 .entry-content > h2 {
  text-align: center;
  margin-top: 80px;
}

/* Author Page */
body.author .author_bio_section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 40px;
  padding-bottom: 40px;
  padding-top: 40px;
  border-bottom: 1px solid var(--border);
  border-top: 1px solid var(--border);
}
body.author .author_bio_section .author-title span {
  font-weight: 400;
}
body.author .author_bio_section .author-avatar img {
  border-radius: 50%;
}
body.author .author_bio_section .author-info {
  text-align: center;
  font-size: 1rem;
}

.category-list {
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.82px;
  color: #8E9BAA;
  margin-bottom: 15px;
  display: block;
}

.posts-list {
  display: flex;
  padding-top: 40px;
}
.posts-list article {
  flex-direction: column;
  padding: 0px;
  gap: 0px !important;
  box-shadow: 0 0 20px rgba(1, 0, 0, 0.1);
  border-radius: 4px;
  width: 33%;
  flex: 1;
  height: fit-content;
}
.posts-list article img.wp-post-image {
  max-height: 150px;
  object-fit: cover;
  width: 100%;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.posts-list article .inside-article {
  padding: 15px 20px 20px 20px;
}
.posts-list article .inside-article span.category-list {
  font-size: 0.6em;
}
.posts-list article .inside-article a.loop-title {
  font-size: 1em;
  font-weight: 600;
  color: #000;
  text-decoration: none;
}
.posts-list article .inside-article div.author-info {
  justify-content: start;
}
.posts-list article .inside-article div.author-info {
  margin: 10px 0px;
}
.posts-list article .inside-article div.entry-summary {
  margin-top: 0px;
  margin-bottom: 10px;
  font-size: 0.7em;
}
.posts-list article .inside-article div.read-more a {
  text-transform: uppercase;
  display: block;
  padding-top: 10px;
  border-top: 1px solid var(--border);
  font-size: 0.75rem;
  font-weight: 700;
  text-decoration: none;
}

section.featured {
  max-width: 1280px;
  margin: 0 auto;
  gap: 40px;
  padding-left: 40px;
  padding-right: 40px;
  flex-wrap: nowrap;
}
@media (max-width: 770px) {
  section.featured article:nth-child(2), section.featured article:nth-child(3) {
    display: none;
  }
  section.featured article img.wp-post-image {
    max-height: 350px !important;
  }
  section.featured span.loop-title {
    font-size: 1.4em !important;
  }
  section.featured div.entry-summary {
    font-size: 1em !important;
  }
}
@media (max-width: 770px) {
  section.featured {
    padding-left: 30px;
    padding-right: 30px;
  }
}

h3.recommended-headline {
  margin-bottom: 0px;
  margin-top: 30px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  text-align: center;
}

section.recommended {
  gap: 20px;
}
@media (max-width: 1050px) {
  section.recommended {
    flex-direction: column;
  }
  section.recommended article {
    width: 100%;
  }
  section.recommended article img.wp-post-image {
    max-height: 350px !important;
  }
}

.shariff_headline {
  text-align: center;
  font-size: 29px;
  margin-bottom: 20px;
  line-height: 1.2em;
  font-weight: 400;
  text-transform: none;
}